import { Person } from "../../../src/scripts/persons/persons";
import { buttonsControl, positionPlayer } from "../../../src/scripts/helpersNavigation";
import { canvas, ctx, playerBullets, colorInfo } from "../../../src/scripts/index.js";
import { Bullet } from "../../../src/scripts/Bullet/Bullet";
import { Sound } from "../../../src/scripts/sound.js";

const playerImg = new Image();
playerImg.src = "../../src/images/player.png";

const hitBulletSound = new Sound("../../../src/sounds/hitBulletSound.mp3");
const deathSound = new Sound("../../../src/sounds/deathSound.mp3");

function Player(x, y, width, height) {
    Person.call(this, x, y, width, height);
    this.health = 20;
    this.score = 0;
    this.frames = 4;
    this.currentFrame = 0;
    this.cadr = 0;
    this.draw = function () {
        //перебор кадров
        if (this.currentFrame == 3) {
            this.cadr = 32;
            this.currentFrame = 0;
        } else if (this.cadr == 32) {
            this.cadr = 0;
        } else {
            this.cadr = this.cadr + 32;
            this.currentFrame++;
        }
        //меняем кажды при движении
        if (buttonsControl.rightPressed) {
            ctx.drawImage(playerImg, this.cadr, 100, 32, 50, this.x, this.y, 32, 50);
        } else if (buttonsControl.leftPressed) {
            ctx.drawImage(playerImg, this.cadr, 50, 32, 50, this.x, this.y, 32, 50);
        } else if (buttonsControl.topPressed) {
            ctx.drawImage(playerImg, this.cadr, 150, 32, 50, this.x, this.y, 32, 50);
        } else if (buttonsControl.bottomPressed) {
            ctx.drawImage(playerImg, this.cadr, 0, 32, 50, this.x, this.y, 32, 50);
        } 
        //сохранение кадра
        switch (positionPlayer) {
            case "right":
                ctx.drawImage(playerImg, 0, 100, 32, 50, this.x, this.y, 32, 50);
                break;
            case "left":
                ctx.drawImage(playerImg, 0, 50, 32, 50, this.x, this.y, 32, 50);
                break;
            case "top":
                ctx.drawImage(playerImg, 0, 150, 32, 50, this.x, this.y, 32, 50);
                break;
            case "bottom":
                ctx.drawImage(playerImg, 0, 0, 32, 50, this.x, this.y, 32, 50);
                break;
            default:
                break;
        }
        //отрисовка жизни и очков 
        ctx.font = "16px Arial";
        ctx.fillStyle = colorInfo;
        ctx.fillText("Lives: " + this.health, 15, 20);
        ctx.fillText("Score: " + this.score, canvas.width - 90, 20);
    };
    this.shoot = function () {
        const bulletPosition = this.midpoint();
        const bulletItem = new Bullet(5, bulletPosition.x, bulletPosition.y);
        playerBullets.push(bulletItem); 
    };
    this.midpoint = function () {
        return {
            x: this.x + this.width / 2,
            y: this.y + this.height / 2
        };
    };
    this.explode = function () {
        this.active = false;
    };
}

export { Player, hitBulletSound, deathSound };

