import { canvas, ctx} from "../../../src/scripts/index.js";
import { bulletMovement } from "../../../src/scripts/helpersNavigation";

const bulletImg = new Image();
bulletImg.src = "../../src/images/antidote.png";
export function Bullet(speed, x, y) {
    this.active = true;
    this.speed = speed;
    this.x = x;
    this.y = y;

    if (bulletMovement.leftSide) {
        this.xVelocity = -this.speed;
        this.yVelocity = 0;
    } else if (bulletMovement.rightSide) {
        this.xVelocity = this.speed;
        this.yVelocity = 0;
    } else if (bulletMovement.topSide) {
        this.xVelocity = 0;
        this.yVelocity = -this.speed;
    } else if (bulletMovement.bottomSide) {
        this.xVelocity = 0;
        this.yVelocity = this.speed;
    }

    this.width = 25;
    this.height = 25;
    this.color = "#000";

    this.inBounds = function () {
        return this.x >= 0 && this.x <= canvas.width &&
        this.y >= 0 && this.y <= canvas.height;
    };

    this.draw = function () {
        ctx.drawImage(bulletImg, this.x, this.y, this.width, this.height);
    };

    this.update = function () {
        this.x += this.xVelocity;
        this.y += this.yVelocity;
        this.active = this.active && this.inBounds();
    };
}