import { Person } from "../../../src/scripts/persons/persons";
import { canvas, ctx, newPlayer} from "../../../src/scripts/index.js";

const enemyImg = new Image();
enemyImg.src = "../../../../src/images/enemy.png";

function Enemy(x, y, width, height) {
    Person.call(this, x, y, width, height);
    this.active = true;
    this.age = Math.floor(Math.random() * 128);
    this.xVelocity = 1;
    this.yVelocity = 0;

    //увеличиваем скорость при опр кол очков
    if (newPlayer.score >= 10) {
        this.xVelocity = 2;
        this.yVelocity = 1;
    }
    if (newPlayer.score >= 20) {
        this.xVelocity = 3;
        this.yVelocity = 2;
    }

    this.inBounds = function () {
        return this.x >= 0 && this.x <= canvas.width &&
        this.y >= 0 && this.y <= canvas.width;
    };

    this.draw = function () {
        ctx.drawImage(enemyImg, this.x, this.y, this.width, this.height);
    };

    this.update = function () {
        this.x += this.xVelocity;
        this.y += this.yVelocity;

        this.yVelocity = 3 * Math.sin(this.age * Math.PI / 64);

        this.age++;

        this.active = this.active && this.inBounds();
    };
    this.explode = function () {
        this.active = false;
    };
}
export { Enemy };

