import { newPlayer, btnPlay } from "../../src/scripts/index";
import { togglePause } from "../../src/scripts/pause";

const buttonsControl = {
    rightPressed: false,
    leftPressed: false,
    topPressed: false,
    bottomPressed: false,
    spacePressed: false,
};
const bulletMovement = {
    rightSide: true,
    leftSide: false,
    topSide: false,
    bottomSide: false,
};
let positionPlayer = "right";
let spaceCount = 0;
let bulletPlay = false;

function keyDownHandler(e) {
    if (e.key == "Right" || e.key == "ArrowRight") {
        buttonsControl.rightPressed = true;
        positionPlayer = "";
        //изменили направление пуль
        bulletMovement.rightSide = true;
        bulletMovement.leftSide = false;
        bulletMovement.topSide = false;
        bulletMovement.bottomSide = false;
    } else if (e.key == "Left" || e.key == "ArrowLeft") {
        buttonsControl.leftPressed = true;
        positionPlayer = "";
        //изменили направление пуль
        bulletMovement.leftSide = true;
        bulletMovement.rightSide = false;
        bulletMovement.topSide = false;
        bulletMovement.bottomSide = false;
    } else if (e.key == "up" || e.key == "ArrowUp") {
        buttonsControl.topPressed = true;
        positionPlayer = "";
        //изменили направление пуль
        bulletMovement.topSide = true;
        bulletMovement.leftSide = false;
        bulletMovement.rightSide = false;
        bulletMovement.bottomSide = false;
    } else if (e.key == "down" || e.key == "ArrowDown") {
        buttonsControl.bottomPressed = true;
        positionPlayer = "";
        //изменили направление пуль
        bulletMovement.bottomSide = true;
        bulletMovement.topSide = false;
        bulletMovement.leftSide = false;
        bulletMovement.rightSide = false;
    } else if (e.key == "space" || e.keyCode == 32) {
        if (spaceCount == 0) {
            newPlayer.shoot();
            spaceCount = 1;
        }
    }
    //pause
    if (e.keyCode === 27) {
        togglePause();
    }
}

function keyUpHandler(e) {
    if (e.key == "Right" || e.key == "ArrowRight") {
        buttonsControl.rightPressed = false;
        positionPlayer = "right";
    } else if (e.key == "Left" || e.key == "ArrowLeft") {
        buttonsControl.leftPressed = false;
        positionPlayer = "left";
    } else if (e.key == "up" || e.key == "ArrowUp") {
        buttonsControl.topPressed = false;
        positionPlayer = "top";
    } else if (e.key == "down" || e.key == "ArrowDown") {
        buttonsControl.bottomPressed = false;
        positionPlayer = "bottom";
    } else if (e.key == "space" || e.keyCode == 32) {
        buttonsControl.spacePressed = false;
        spaceCount = 0;
    }
}

function getMousePosition(e) {
    var rect = canvas.getBoundingClientRect();
    return {
        x: e.clientX - rect.left,
        y: e.clientY - rect.top
    };
}
// обработчик события hover
function onmousemove(e) {
    var mouse = getMousePosition(e);
    if (mouse.x > btnPlay.x && mouse.x < btnPlay.x+ btnPlay.w && mouse.y > btnPlay.y && mouse.y < btnPlay.y + btnPlay.h) {
        btnPlay.fontSize = "26px";
    } else {
        btnPlay.fontSize = "25px";
    }
}
// Оработчик события click
function onmouseup(e) {
    var mouse = getMousePosition(e);
    if (mouse.x > btnPlay.x && mouse.x < btnPlay.x+ btnPlay.w && mouse.y > btnPlay.y && mouse.y < btnPlay.y + btnPlay.h) {
        bulletPlay = true;
    }
}
export { keyUpHandler, keyDownHandler, getMousePosition, onmouseup, onmousemove, buttonsControl, bulletMovement, positionPlayer, bulletPlay };