import { playerBullets, enemies, newPlayer } from "../../src/scripts/index.js";
import { hitBulletSound } from "../scripts/persons/player";

function collisions(a, b) {
    return a.x < b.x + b.width &&
        a.x + a.width > b.x &&
        a.y < b.y + b.height &&
        a.y + a.height > b.y;
}
export function handleCollisions() {
    playerBullets.forEach(function (bulletItem) {
        enemies.forEach(function (enemyItem) {
            if (collisions(bulletItem, enemyItem)) {
                enemyItem.explode();
                bulletItem.active = false;
                //попала пуля
                newPlayer.score++;
            }
        });
    });
    enemies.forEach(function (enemyItem) {
        if (collisions(enemyItem, newPlayer)) {
            enemyItem.explode();
            newPlayer.explode();
            //врезались в игрока
            newPlayer.health--;
            hitBulletSound.play();

        }
    });
}
