import { colorBgStartWindow, colorWhite } from "../../../src/scripts/index.js";
import { bulletPlay } from "../../../src/scripts/helpersNavigation";
function Button(x, y, w, h, state, image, text, fontSize) {
    this.x = x;
    this.y = y;
    this.w = w;
    this.h = h;
    this.state = state;
    this.imageShift = 0;
    this.image = image;
    this.text = text;
    this.fontSize = fontSize;
}

function drawButton(ctx, button) {
    if (!bulletPlay) {
        ctx.fillStyle = colorBgStartWindow;
        ctx.fillRect(0, 0, canvas.width, canvas.height);
        ctx.font = button.fontSize + " Arial";
        ctx.fillStyle = colorWhite;
        ctx.drawImage(button.image, 0, button.imageShift, button.w, button.h, button.x, button.y, button.w, button.h);
        ctx.fillText(button.text, button.x + button.w / 3, button.y + button.h / 2 + 5);
        ctx.font = "25px Arial";
        ctx.fillText("Press Play to start the game", button.x - 80, button.y - 40);
        ctx.fillText("Press Ecs to pause", 20, canvas.height - 25 );
        ctx.fillText("Use the arrow buttons to move", button.x - 95, button.y + 110);
        ctx.fillText("Press Space to shoot", button.x - 45, button.y + 140);
        
    }
}

export { Button, drawButton };