import { keyUpHandler, keyDownHandler, buttonsControl, onmouseup, onmousemove, bulletPlay } from "../scripts/helpersNavigation";
import { Player, deathSound, hitBulletSound } from "../scripts/persons/player";
import { Enemy } from "../scripts/persons/enemy";
import { handleCollisions } from "../scripts/collisions";
import { paused } from "../scripts/pause";
import { Button, drawButton } from "../scripts/Buttons/Buttons";
import { recRecord } from "../scripts/Records/Records";

export const canvas = document.getElementById("canvas");
export const ctx = canvas.getContext("2d");
canvas.width = window.innerWidth;
canvas.height = window.innerHeight;
//снаряды
export let playerBullets = [];
//враги
export let enemies = [];
//создание игрока
export const newPlayer = new Player(canvas.width/2 - 50, canvas.height/2 - 30, 32, 32);
//buttons
export const btnPlayImage = new Image();
btnPlayImage.src = '../../src/images/btnDark.png';
export const btnPlay = new Button(canvas.width/2 - 90, canvas.height/2 - 30, 150, 66, 'normal', btnPlayImage, 'Play', '25px');
//color varibles
export const colorInfo ="#0095DD";
export const colorWarn ="#ff0000";
export const colorBgStartWindow ="#2F2F2F";
export const colorWhite ="#ffffff";

document.addEventListener("keydown", keyDownHandler, false);
document.addEventListener("keyup", keyUpHandler, false);
document.addEventListener("click", onmouseup, false);
document.addEventListener("mousemove", onmousemove, false);

function loop() {
    requestAnimationFrame(loop);
    draw();
    checkUpdate();
}
loop();

function checkUpdate() {
    if(bulletPlay) { 
        if (newPlayer.health > 0) {
            if (!paused) {
                update();
            }
        }
    }
}

function update() {
    //движение игрока
    if (buttonsControl.rightPressed && newPlayer.x < canvas.width - newPlayer.width) {
        newPlayer.x += 7;
    } else if (buttonsControl.leftPressed && newPlayer.x > 0) {
        newPlayer.x -= 7;
    }
    if (buttonsControl.topPressed && newPlayer.y > 0) {
        newPlayer.y -= 7;
    } else if (buttonsControl.bottomPressed && newPlayer.y < canvas.height - newPlayer.height - 25) {
        newPlayer.y += 7;
    }
    //стрельба
    playerBullets.forEach(function (bulletItem) {
        bulletItem.update();
    });
    playerBullets = playerBullets.filter(function (bulletItem) {
        return bulletItem.active;
    });
    //враги
    enemies.forEach(function (enemyItem) {
        enemyItem.update();
    });
    enemies = enemies.filter(function (enemyItem) {
        return enemyItem.active;
    });
    if (Math.random() < 0.1) {
        const enemyItem = new Enemy(0, canvas.height / 4 + Math.random() * canvas.height / 2, 15, 15);
        enemies.push(enemyItem);
    }
    if (newPlayer.health <= 10) {
        canvas.classList.add("canvasLowHealth");
    }
    //столкновения
    handleCollisions();
}
function draw() {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    newPlayer.draw();
    playerBullets.forEach(function (bulletItem) {
        bulletItem.draw();
    });
    //враги
    enemies.forEach(function (enemyItem) {
        enemyItem.draw();
    });
    
    drawButton(ctx, btnPlay);
    //проигрыш
    if (newPlayer.health <= 0) {        
        recRecord(newPlayer.score);
        ctx.font = "30px Arial";
        ctx.fillStyle = colorWarn;
        ctx.fillText("YOU LOOSE", canvas.width/2 - 90, canvas.height/2 - 30);
        ctx.fillText("You score: " + newPlayer.score, canvas.width/2 - 85, canvas.height/2 + 20);
        ctx.fillText("You records: " + localStorage.record, canvas.width/2 - 115, canvas.height/2 + 70);
        canvas.classList.remove("canvasLowHealth");
        hitBulletSound.stop();
        deathSound.play();
    }
    //pause
    if(bulletPlay) { 
        if(paused) { 
            if (newPlayer.health > 0) {
                ctx.font = "30px Arial";
                ctx.fillStyle = colorWarn;
                ctx.fillText("PAUSE", canvas.width/2 - 60, canvas.height/2);
            }
        }
    }
    
}

